<template>
<div :style='{"width":"calc(100% - 40px)","padding":"30px 20px 40px","margin":"20px auto 0","position":"relative","background":"rgba(255,255,255,.3)"}'>
    <el-form
      class="add-update-preview"
      ref="ruleForm"
      :model="ruleForm"
      :rules="rules"
      label-width="80px"
    >
          <el-form-item :style='{"border":"0px solid #dfdfdf","padding":"10px","boxShadow":"0px 0px 0px #eee","margin":"0 0 8px 0","borderRadius":"8px","background":"none"}' label="" prop="xianlumingcheng">
            <el-input v-model="ruleForm.xianlumingcheng" 
                placeholder="" clearable ></el-input>
          </el-form-item>
          <el-form-item :style='{"border":"0px solid #dfdfdf","padding":"10px","boxShadow":"0px 0px 0px #eee","margin":"0 0 8px 0","borderRadius":"8px","background":"none"}' label="" prop="xianlutupian">
            <file-upload
            tip=""
            action="file/upload"
            :limit="3"
            :multiple="true"
            :fileUrls="ruleForm.xianlutupian?ruleForm.xianlutupian:''"
            @change="xianlutupianUploadChange"
            ></file-upload>
          </el-form-item>
          <el-form-item :style='{"border":"0px solid #dfdfdf","padding":"10px","boxShadow":"0px 0px 0px #eee","margin":"0 0 8px 0","borderRadius":"8px","background":"none"}'  label="" prop="xianluleixing">
            <el-select v-model="ruleForm.xianluleixing" placeholder="" >
              <el-option
                  v-for="(item,index) in xianluleixingOptions"
                  :key="index"
                  :label="item"
                  :value="item">
              </el-option>
            </el-select>
          </el-form-item>
          <el-form-item :style='{"border":"0px solid #dfdfdf","padding":"10px","boxShadow":"0px 0px 0px #eee","margin":"0 0 8px 0","borderRadius":"8px","background":"none"}' label="" prop="jingdianmingcheng">
            <el-select  @change="jingdianmingchengChange" v-model="ruleForm.jingdianmingcheng" placeholder="">
              <el-option
                  v-for="(item,index) in jingdianmingchengOptions"
                  :key="index"
                  :label="item"
                  :value="item">
              </el-option>
            </el-select>
          </el-form-item>
          <el-form-item :style='{"border":"0px solid #dfdfdf","padding":"10px","boxShadow":"0px 0px 0px #eee","margin":"0 0 8px 0","borderRadius":"8px","background":"none"}' label="" prop="dengji">
            <el-input v-model="ruleForm.dengji" 
                placeholder="" clearable ></el-input>
          </el-form-item>
          <el-form-item :style='{"border":"0px solid #dfdfdf","padding":"10px","boxShadow":"0px 0px 0px #eee","margin":"0 0 8px 0","borderRadius":"8px","background":"none"}' label="" prop="jingdianweizhi">
            <el-input v-model="ruleForm.jingdianweizhi" 
                placeholder="" clearable ></el-input>
          </el-form-item>
          <el-form-item :style='{"border":"0px solid #dfdfdf","padding":"10px","boxShadow":"0px 0px 0px #eee","margin":"0 0 8px 0","borderRadius":"8px","background":"none"}' label="" prop="qidian">
            <el-input v-model="ruleForm.qidian" 
                placeholder="" clearable ></el-input>
          </el-form-item>
          <el-form-item :style='{"border":"0px solid #dfdfdf","padding":"10px","boxShadow":"0px 0px 0px #eee","margin":"0 0 8px 0","borderRadius":"8px","background":"none"}' label="" prop="tujingluduan">
            <el-input v-model="ruleForm.tujingluduan" 
                placeholder="" clearable ></el-input>
          </el-form-item>
          <el-form-item :style='{"border":"0px solid #dfdfdf","padding":"10px","boxShadow":"0px 0px 0px #eee","margin":"0 0 8px 0","borderRadius":"8px","background":"none"}' label="" prop="zhongdian">
            <el-input v-model="ruleForm.zhongdian" 
                placeholder="" clearable ></el-input>
          </el-form-item>
          <el-form-item :style='{"border":"0px solid #dfdfdf","padding":"10px","boxShadow":"0px 0px 0px #eee","margin":"0 0 8px 0","borderRadius":"8px","background":"none"}' label="" prop="jiaotongfangshi">
            <el-input v-model="ruleForm.jiaotongfangshi" 
                placeholder="" clearable ></el-input>
          </el-form-item>
          <el-form-item :style='{"border":"0px solid #dfdfdf","padding":"10px","boxShadow":"0px 0px 0px #eee","margin":"0 0 8px 0","borderRadius":"8px","background":"none"}' label="" prop="xianluxiangqing">
            <editor 
                :style='{"minHeight":"200px","padding":"0","margin":"0","borderColor":"#ccc","backgroundColor":"#fff","borderRadius":"0","borderWidth":"0px","width":"100%","borderStyle":"solid","height":"auto"}'
                v-model="ruleForm.xianluxiangqing" 
                class="editor" 
                action="file/upload">
            </editor>
          </el-form-item>

      <el-form-item :style='{"padding":"0","margin":"20px 0 20px 0"}'>
        <el-button :style='{"border":"0","cursor":"pointer","padding":"0","boxShadow":"0px 0px 0px #ccc","margin":"0 20px 0 0","color":"#fff","outline":"none","borderRadius":"0px","background":"linear-gradient(320deg, rgba(48,134,185,1) 0%, rgba(197,230,250,1) 80%, rgba(48,134,185,1) 100%),#3086b9","width":"110px","lineHeight":"40px","fontSize":"14px","height":"40px"}'  type="primary" @click="onSubmit"></el-button>
        <el-button :style='{"border":"0px solid #bbb","cursor":"pointer","padding":"0","boxShadow":"0px 0px 0px #eee","margin":"0","color":"#333","outline":"none","borderRadius":"0px","background":"linear-gradient(320deg, rgba(204,204,204,1) 0%, rgba(255,255,255,1) 80%, rgba(204,204,204,1) 100%),#999","width":"110px","lineHeight":"40px","fontSize":"14px","height":"40px"}' @click="back()"></el-button>
      </el-form-item>
    </el-form>
</div>
</template>

<script>
  export default {
    data() {
      return {
        id: '',
        type: '',
        userTableName: localStorage.getItem('UserTableName'),
        ruleForm: {
          xianlumingcheng: '',
          xianlutupian: '',
          xianluleixing: '',
          jingdianmingcheng: '',
          dengji: '',
          jingdianweizhi: '',
          qidian: '',
          tujingluduan: '',
          zhongdian: '',
          jiaotongfangshi: '',
          xianluxiangqing: '',
        },
        xianluleixingOptions: [],
        jingdianmingchengOptions: [],
        rules: {
          xianlumingcheng: [
            { required: true, message: '', trigger: 'blur' },
          ],
          xianlutupian: [
          ],
          xianluleixing: [
          ],
          jingdianmingcheng: [
            { required: true, message: '', trigger: 'blur' },
          ],
          dengji: [
          ],
          jingdianweizhi: [
          ],
          qidian: [
          ],
          tujingluduan: [
          ],
          zhongdian: [
          ],
          jiaotongfangshi: [
          ],
          xianluxiangqing: [
          ],
          clicktime: [
          ],
          clicknum: [
            { validator: this.$validate.isIntNumer, trigger: 'blur' },
          ],
        },
      };
    },
    computed: {



    },
    created() {
	  //this.bg();
      let type = this.$route.query.type ? this.$route.query.type : '';
      this.init(type);
    },
    methods: {
      getMakeZero(s) {
          return s < 10 ? '0' + s : s;
      },
      // 
      download(file){
        window.open(`${file}`)
      },
      // 
      init(type) {
        this.type = type;
        if(type=='cross'){
          var obj = JSON.parse(localStorage.getItem('crossObj'));
          for (var o in obj){
            if(o=='xianlumingcheng'){
              this.ruleForm.xianlumingcheng = obj[o];
              continue;
            }
            if(o=='xianlutupian'){
              this.ruleForm.xianlutupian = obj[o].split(",")[0];
              continue;
            }
            if(o=='xianluleixing'){
              this.ruleForm.xianluleixing = obj[o];
              continue;
            }
            if(o=='jingdianmingcheng'){
              this.ruleForm.jingdianmingcheng = obj[o];
              continue;
            }
            if(o=='dengji'){
              this.ruleForm.dengji = obj[o];
              continue;
            }
            if(o=='jingdianweizhi'){
              this.ruleForm.jingdianweizhi = obj[o];
              continue;
            }
            if(o=='qidian'){
              this.ruleForm.qidian = obj[o];
              continue;
            }
            if(o=='tujingluduan'){
              this.ruleForm.tujingluduan = obj[o];
              continue;
            }
            if(o=='zhongdian'){
              this.ruleForm.zhongdian = obj[o];
              continue;
            }
            if(o=='jiaotongfangshi'){
              this.ruleForm.jiaotongfangshi = obj[o];
              continue;
            }
            if(o=='xianluxiangqing'){
              this.ruleForm.xianluxiangqing = obj[o];
              continue;
            }
            if(o=='clicktime'){
              this.ruleForm.clicktime = obj[o];
              continue;
            }
            if(o=='clicknum'){
              this.ruleForm.clicknum = obj[o];
              continue;
            }
          }
        }
        // 
        this.$http.get(this.userTableName + '/session', {emulateJSON: true}).then(res => {
          if (res.data.code == 0) {
            var json = res.data.data;
          }
        });
        this.$http.get('option/xianluleixing/xianluleixing', {emulateJSON: true}).then(res => {
          if (res.data.code == 0) {
            this.xianluleixingOptions = res.data.data;
          }
        });
        this.$http.get('option/remenjingdian/jingdianmingcheng', {emulateJSON: true}).then(res => {
          if (res.data.code == 0) {
            this.jingdianmingchengOptions = res.data.data;
          }
        });
      },
      // 
      jingdianmingchengChange () {
        this.$http.get('follow/remenjingdian/jingdianmingcheng?columnValue=' + this.ruleForm.jingdianmingcheng, {emulateJSON: true}).then(res => {
          if (res.data.code == 0) {
            if(res.data.data.dengji){
              this.ruleForm.dengji = res.data.data.dengji
            }
            if(res.data.data.jingdianweizhi){
              this.ruleForm.jingdianweizhi = res.data.data.jingdianweizhi
            }
          }
        });
      },

    // 
      // 
      info(id) {
        this.$http.get('lvyouxianlu/detail/${id}', {emulateJSON: true}).then(res => {
          if (res.data.code == 0) {
            this.ruleForm = res.data.data;
          }
        });
      },
      // 
      onSubmit() {

        //
        var crossuserid;
        var crossrefid;
        var crossoptnum;
        this.$refs["ruleForm"].validate(valid => {
          if(valid) {
            if(this.type=='cross'){
                 var statusColumnName = localStorage.getItem('statusColumnName');
                 var statusColumnValue = localStorage.getItem('statusColumnValue');
                 if(statusColumnName && statusColumnName!='') {
                     var obj = JSON.parse(localStorage.getItem('crossObj'));
                     if(!statusColumnName.startsWith("[")) {
                         for (var o in obj){
                             if(o==statusColumnName){
                                 obj[o] = statusColumnValue;
                             }
                         }
                         var table = localStorage.getItem('crossTable');
                         this.$http.post(table+'/update', obj).then(res => {});
                     } else {
                            crossuserid=Number(localStorage.getItem('userid'));
                            crossrefid=obj['id'];
                            crossoptnum=localStorage.getItem('statusColumnName');
                            crossoptnum=crossoptnum.replace(/\[/,"").replace(/\]/,"");
                     }
                 }
            }
            if(crossrefid && crossuserid) {
                 this.ruleForm.crossuserid=crossuserid;
                 this.ruleForm.crossrefid=crossrefid;
                 var params = {
                     page: 1,
                     limit: 10,
                     crossuserid:crossuserid,
                     crossrefid:crossrefid,
                 }
                 this.$http.get('lvyouxianlu/list', {
                  params: params
                 }).then(res => {
                     if(res.data.data.total>=crossoptnum) {
                         this.$message({
                          message: localStorage.getItem('tips'),
                          type: 'success',
                          duration: 1500,
                         });
                          return false;
                     } else {
                         // 


                          this.$http.post('lvyouxianlu/add', this.ruleForm).then(res => {
                              if (res.data.code == 0) {
                                  this.$message({
                                      message: '',
                                      type: 'success',
                                      duration: 1500,
                                      onClose: () => {
                                          this.$router.go(-1);
                                      }
                                  });
                              } else {
                                  this.$message({
                                      message: res.data.msg,
                                      type: 'error',
                                      duration: 1500
                                  });
                              }
                          });
                     }
                 });
             } else {


                  this.$http.post('lvyouxianlu/add', this.ruleForm).then(res => {
                     if (res.data.code == 0) {
                          this.$message({
                              message: '',
                              type: 'success',
                              duration: 1500,
                              onClose: () => {
                                  this.$router.go(-1);
                              }
                          });
                      } else {
                          this.$message({
                              message: res.data.msg,
                              type: 'error',
                              duration: 1500
                          });
                      }
                  });
             }
          }
        });
      },
      // uuid
      getUUID () {
        return new Date().getTime();
      },
      // 
      back() {
        this.$router.go(-1);
      },
      xianlutupianUploadChange(fileUrls) {
          this.ruleForm.xianlutupian = fileUrls.replace(new RegExp(this.$config.baseUrl,"g"),"");;
      },
    }
  };
</script>

